
$brand-blue: #08a6e4;
$brand-red: #fb3b64;
$brand-green: #55dfb4;
$brand-text-grey: #a5a5a5;

html {
  font-size: 16px;
}

p {
  font-family: "Open Sans", sans-serif;
}

h1,
h2,
h3 {
  font-family: "Raleway", sans-serif;
}

body {
  width: 100%;
  margin: 0;

  display: flex;
  justify-content: center;
  flex-direction: column;
}

nav {
  width: 100vw;
  -webkit-box-shadow: 0px 5px 24px 0px rgba(66, 68, 90, 1);
  -moz-box-shadow: 0px 5px 24px 0px rgba(66, 68, 90, 1);
  box-shadow: 0px 5px 24px 0px rgba(66, 68, 90, 1);
  overflow: hidden;
}

.nav-bar {
  position: sticky;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.logo {
  margin-left: 100px;
  font-size: 2rem;
  color: $brand-blue;
}

.menu {
  margin-right: 100px;
  font-size: 1.3rem;
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
}

.menu a {
  display: inline-block;
  font-weight: 600;
  letter-spacing: 2px;
  position: relative;
  text-decoration: none;
  color: $brand-text-grey;
  margin: 0 55px;
}

.menu a:hover {
  color: black;
}

.menu a::after {
  content: "";
  background-color: #08a6e400;
  width: 100%;
  height: 6px;
  position: absolute;
  left: 0;
  top: 61px;
  transition: background-color 0.5s;
}

.menu a:hover::after {
  background-color: $brand-blue;
}

